// Typography
$base-font-family: "Arial";
$heading-font-family: $base-font-family;

// Font Sizes
$base-font-size: 1em;

// Line height
$base-line-height: 1.5;
$heading-line-height: 1.2;

// Section Margins
$mobile-section-margin: 4em;
$desk-section-margin: em(100px);

// Other Sizes
$base-border-radius: 3px;
$base-spacing: $base-line-height * 1em;
$small-spacing: $base-spacing / 3;
$base-z-index: 0;

// Colors
$blue: #0b406b;
$light-blue: #5dc3bf;
$black: #111;
$dark-gray: #222;
$medium-gray: #777;
$light-gray: #eee;
$white: #fff;

// Font Colors
$base-font-color: #555;
$heading-font-color: $black;
$primary-color: $blue;
$action-color: $light-blue;

// Border
$base-border-color: $light-gray;
$base-border: 1px solid $base-border-color;

// Background Colors
$base-background-color: #fff;
$secondary-background-color: #f1f1f1;

// Forms
$form-box-shadow: inset 0 1px 3px rgba(#000, 0.06);
$form-box-shadow-focus: $form-box-shadow,
  0 0 5px adjust-color($action-color, $lightness: -5%, $alpha: -0.3);

// Animations
$base-duration: 300ms;
$medium-duration: 500ms;
$long-duration: 800ms;
$base-timing: ease;
