fieldset {
  background-color: transparent;
  border: 0;
  margin: 0;
  padding: 0;
}

legend {
  font-weight: 600;
  margin-bottom: 0.25em;
  padding: 0;
}

label {
  display: inline-block;
  font-weight: 600;
  margin-bottom: 0.25em;
}

input,
select {
  display: block;
  font-family: "Arial";
  font-size: 1em;
}

input,
select[multiple] {
  background-color: #fff;
  border: 1px solid #eee;
  border-radius: 3px;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  font-family: "Arial";
  font-size: 1em;
  margin-bottom: 0.5em;
  padding: 0.5em;
  transition: border-color 300ms ease;
  width: 100%;
}
input:hover,
select[multiple]:hover {
  border-color: shade(#eee, 20%);
}
input:focus,
select[multiple]:focus {
  border-color: #5dc3bf;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.06), 0 0 5px rgba(74, 188, 184, 0.7);
  outline: none;
}
input:disabled,
select[multiple]:disabled {
  background-color: shade(#fff, 5%);
  cursor: not-allowed;
}
input:disabled:hover,
select[multiple]:disabled:hover {
  border: 1px solid #eee;
}

textarea {
  resize: vertical;
}

[type=search] {
  appearance: none;
}

[type=checkbox],
[type=radio] {
  display: inline;
  margin-right: 0.25em;
}

[type=file] {
  margin-bottom: 0.5em;
  width: 100%;
}

select {
  margin-bottom: 1.5em;
  max-width: 100%;
  width: auto;
}

._form select {
  width: 100%;
}
._form button {
  margin-top: 1em;
}

.site-inner .gform_wrapper {
  margin: 0;
}
.site-inner .gform_wrapper ul {
  margin-left: 0px !important;
}
.site-inner .gform_wrapper ul li {
  list-style-type: none !important;
  padding: 0 !important;
}
.site-inner .gform_wrapper ul li:first-child {
  margin-top: 0;
}
.site-inner .gform_wrapper ul li input,
.site-inner .gform_wrapper ul li select {
  width: 100% !important;
}
.site-inner .gform_wrapper .gform_footer {
  margin: 0;
}

#tinymce ul,
.site-inner ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  margin-bottom: 28px;
}
#tinymce ul li,
.site-inner ul li {
  list-style-type: none;
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 0.625rem;
}
#tinymce ul li:before,
.site-inner ul li:before {
  content: "";
  height: 8px;
  width: 8px;
  background-color: #222;
  border-radius: 50%;
  position: absolute;
  left: 3px;
  top: 7px;
}
#tinymce ul li ul,
.site-inner ul li ul {
  margin-top: 0.5em;
  margin-bottom: 0;
}

#tinymce ol,
.site-inner ol {
  margin-left: 0;
  margin: 0;
  padding: 0;
  margin-bottom: 1.5em;
}
#tinymce ol li,
.site-inner ol li {
  position: relative;
  margin-left: 1.5rem;
  margin-bottom: 0.75rem;
}
#tinymce ol li ol,
.site-inner ol li ol {
  margin-top: 0.5em;
  margin-bottom: 0;
}

dl {
  margin-bottom: 1.5em;
}
dl dt {
  margin-top: 0.5em;
}
dl dd {
  margin: 0;
}

> dl,
> ol,
> ul,
p {
  margin-bottom: 1.5em;
}

table {
  border-collapse: collapse;
  margin: 0.5em 0;
  table-layout: fixed;
  width: 100%;
}

th {
  border-bottom: 1px solid shade(#eee, 25%);
  font-weight: 600;
  padding: 0.5em 0;
  text-align: left;
}

td {
  border-bottom: 1px solid #eee;
  padding: 0.5em 0;
}

tr,
td,
th {
  vertical-align: middle;
}

body {
  color: #555;
  font-family: "Arial";
  font-size: 90%;
  line-height: 1.5;
}
body *::selection {
  background-color: #5dc3bf;
  color: white;
}
@media screen and (min-width: 900px) {
  body {
    font-size: 1em;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Arial";
  color: #111;
  font-size: 1em;
  font-weight: 500;
  line-height: 1.2;
  margin: 0 0 0.5em;
}

h1,
#heading-one {
  font-size: 3.25em;
  font-weight: bold;
  font-family: "Arial";
  display: block;
}

h2,
#post-title {
  font-size: 2.5em;
  font-weight: bold;
}

h3 {
  font-size: 2.125em;
}

h4 {
  font-size: 1.75em;
}

h5 {
  font-size: 1.625em;
}

h6 {
  font-size: 1.25em;
}

p {
  margin: 0 0 1.5em;
}

a {
  color: #5dc3bf;
  text-decoration: none;
  transition: all 300ms ease;
}
a:active, a:focus, a:hover {
  color: shade(#5dc3bf, 25%);
}

hr {
  border-bottom: 1px solid #eee;
  border-left: 0;
  border-right: 0;
  border-top: 0;
  margin: 1.5em 0;
}

img,
picture {
  margin: 0;
  max-width: 100%;
}

blockquote {
  border-left: 1px solid #eee;
  border-color: #0b406b;
  border-width: 7px;
  margin: 4% 5% 4% 0;
  padding: 0 0 0 1.25rem;
}

code {
  background: #eee;
  border: 1px solid #eee;
  color: #555;
  padding: 2px 4px;
}

.wp-caption .wp-caption-text {
  margin: 10px 0;
  font-size: 0.75em;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 1.2;
}

.base-font {
  font-family: "Arial";
}

.heading-font {
  font-family: "Arial";
}

.uppercase {
  text-transform: uppercase;
}

.normalcase {
  text-transform: none;
}

.lowercase {
  text-transform: lowercase;
}

.small {
  font-size: 0.875em;
}

.large {
  font-size: 1.125em;
}

.light-weight {
  font-weight: 300;
}

.normal-weight {
  font-weight: 400;
}

.bold-weight {
  font-weight: 700;
}

.no-bottom-margin {
  margin-bottom: -0.5em;
  display: block;
}

.aligncenter {
  display: block;
  margin: 0 auto 1.5em;
  text-align: center;
}

.alignleft {
  margin: 0 1.5em 1.5em 0;
}

.alignright {
  margin: 0 0 1.5em 1.5em;
}

.blue-font {
  color: #0b406b;
}

.light-blue-font {
  color: #5dc3bf;
}

.white-font {
  color: #fff;
}

.dark-gray-font {
  color: #222;
}

.medium-gray-font {
  color: #777;
}

.light-gray-font {
  color: #eee;
}

#tinymce {
  background: #e5e5e5;
}

._form button,
input[type=button],
input[type=reset],
input[type=submit],
.primary-button a,
.blue-button a,
.light-blue-button a,
.medium-gray-button a,
.dark-gray-button a,
.white-button a {
  -webkit-font-smoothing: antialiased;
  appearance: none;
  background-color: #5dc3bf;
  border: 2px solid transparent;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: "Arial";
  font-size: 1.125em;
  font-weight: bold;
  line-height: 1;
  padding: 1em 3.75em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 300ms ease;
  user-select: none;
  vertical-align: middle;
}
._form button:hover, ._form button:focus,
input[type=button]:hover,
input[type=button]:focus,
input[type=reset]:hover,
input[type=reset]:focus,
input[type=submit]:hover,
input[type=submit]:focus,
.primary-button a:hover,
.primary-button a:focus,
.blue-button a:hover,
.blue-button a:focus,
.light-blue-button a:hover,
.light-blue-button a:focus,
.medium-gray-button a:hover,
.medium-gray-button a:focus,
.dark-gray-button a:hover,
.dark-gray-button a:focus,
.white-button a:hover,
.white-button a:focus {
  background-color: transparent;
  color: #5dc3bf;
  border-color: #5dc3bf;
}
._form button:disabled,
input[type=button]:disabled,
input[type=reset]:disabled,
input[type=submit]:disabled,
.primary-button a:disabled,
.blue-button a:disabled,
.light-blue-button a:disabled,
.medium-gray-button a:disabled,
.dark-gray-button a:disabled,
.white-button a:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}
._form button:disabled:hover,
input[type=button]:disabled:hover,
input[type=reset]:disabled:hover,
input[type=submit]:disabled:hover,
.primary-button a:disabled:hover,
.blue-button a:disabled:hover,
.light-blue-button a:disabled:hover,
.medium-gray-button a:disabled:hover,
.dark-gray-button a:disabled:hover,
.white-button a:disabled:hover {
  background-color: transparent;
}

.small-button {
  display: inline-block;
}
.small-button a {
  font-size: 0.875em;
  padding: 1em 3em;
}

.primary-button a,
.blue-button a,
.blue-button input[type=submit] {
  background-color: #0b406b;
}
.primary-button a:hover, .primary-button a:focus,
.blue-button a:hover,
.blue-button a:focus,
.blue-button input[type=submit]:hover,
.blue-button input[type=submit]:focus {
  color: #0b406b;
  border-color: #0b406b;
  outline: none;
}

.light-blue-button a,
.light-blue-button input[type=submit] {
  background-color: #5dc3bf;
}
.light-blue-button a:hover, .light-blue-button a:focus,
.light-blue-button input[type=submit]:hover,
.light-blue-button input[type=submit]:focus {
  color: #5dc3bf;
  border-color: #5dc3bf;
  outline: none;
}

.medium-gray-button a,
.medium-gray-button input[type=submit] {
  background-color: #777;
}
.medium-gray-button a:hover, .medium-gray-button a:focus,
.medium-gray-button input[type=submit]:hover,
.medium-gray-button input[type=submit]:focus {
  color: #777;
  border-color: #777;
  outline: none;
}

.dark-gray-button a,
.dark-gray-button input[type=submit] {
  background-color: #222;
}
.dark-gray-button a:hover, .dark-gray-button a:focus,
.dark-gray-button input[type=submit]:hover,
.dark-gray-button input[type=submit]:focus {
  color: #222;
  border-color: #222;
  outline: none;
}

.white-button a,
.white-button input[type=submit] {
  background-color: white;
  color: #222;
}
.white-button a:hover, .white-button a:focus,
.white-button input[type=submit]:hover,
.white-button input[type=submit]:focus {
  color: white;
  border-color: white;
  outline: none;
}

.blue-button,
.light-blue-button,
.medium-gray-button,
.dark-gray-button,
.white-button {
  display: inline-block;
}